@import "../styles/index.scss";

.buttons__container {
  padding: 30px 10px;
  height: 65vh;
  width: 90%;
  position: relative;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(2, 1fr);
  justify-items: center;
  align-items: center;
}

.background {
  background-image: url("../assets/bg-triangle.svg");
  background-repeat: no-repeat;
  background-size: 70%;
  background-position: center;
}

.description {
  align-self: start;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  font-weight: 600;
  font-size: 0.86rem;
}

.left {
  grid-column: 1 / 3;
  grid-row: 2 / 2;
}

.right {
  grid-column: 3 / 5;
  grid-row: 2 / 2;
}

.result {
  grid-column: 1 / 5;
  grid-row: 2 / 3;
  text-transform: uppercase;
  font-weight: 800;
  font-size: 3rem;
  letter-spacing: 0.03em;
}

@media (min-width: 540px) {
  .buttons__container {
    height: 70vh;
    padding: 10px 10px;
  }
}

@media (min-width: 1024px) {
  .buttons__container {
    height: 80vh;
    padding: 10px 10px;
  }
}

@media (min-width: 1080px) {
  .buttons__container {
    width: 50%;
  }

  .background {
    background-size: 40%;
  }
}

@media (min-width: 1080px) {
  .buttons__container {
    width: 50%;
  }

  .background {
    background-size: 40%;
  }
}

@media (min-width: 1280px) {
  .buttons__container {
    width: 50%;
    height: 70vh;
  }

  .background {
    background-size: 50%;
  }
}
