.rules-container {
  position: fixed;
  top: -10px;
  left: -10px;
  right: -10px;
  bottom: -10px;
  background-color: rgba($color: #000000, $alpha: 0.6);
}

.rules {
  position: fixed;
  background-color: white;
  top: -10px;
  left: -10px;
  right: -10px;
  bottom: -10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #3b4363;
  z-index: 1;
  gap: 60px;
  img {
    width: 80%;
  }
}

@media (min-width: 1280px) {
  .rules {
    width: 28%;
    height: 45vh;
    border-radius: 5px;
    margin: auto;
    gap: 0;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto 1fr;
    h1 {
      grid-column: 1 / 2;
      padding-left: 30px;
    }
    img {
      width: 70%;
      order: 2;
      grid-column: 1 / 3;
      justify-self: center;
    }
  }
}

@media (min-width: 1920px) {
  .rules {
    width: 20%;
    img {
      width: 80%;
    }
  }
}
