@import "./index.scss";

button {
  width: 50%;
  height: 45px;
  background-color: transparent;
  @include flex-center;
  font-family: $font;
  &:hover {
    cursor: pointer;
  }
  img {
    width: 20px;
    height: 20px;
  }
}

.transparent {
  border-radius: 10px;
  border: 1px solid rgba($color: #fff, $alpha: 0.9);
  color: rgba($color: #fff, $alpha: 0.9);
  font-weight: 600;
  font-size: 0.88rem;
  letter-spacing: 0.1em;
  width: 30%;
  height: 36px;
}

.light {
  border-radius: 10px;
  background-color: rgba($color: #fff, $alpha: 0.9);
  border: none;
  color: none;
  font-weight: 800;
  font-size: 1rem;
  letter-spacing: 0.2em;
}

.close {
  border-radius: 100%;
  width: 50px;
  height: 50px;
  border: none;
  background-image: url("../assets/icon-close.svg");
  background-repeat: no-repeat;
  background-size: 20px;
  background-position: center;
}

.go-back {
  grid-column: 1 / 5;
  grid-row: 2 / 3;
  align-self: end;
}

@media (min-width: 1280px) {
  .transparent {
    align-self: end;
    width: 9%;
    margin-right: 50px;
  }
  .close {
    grid-column: 2 / 3;
    order: 0;
    justify-self: end;
    margin-right: 10px;
    margin-top: 10px;
  }
}
