@import "./index.scss";

.buttons__button {
  position: relative;
  width: 80%;
  height: 75%;
  border: 10px solid transparent;
  border-radius: 100%;
  @include flex-center;
  z-index: 2;
  img {
    width: 45%;
  }
}

.rock {
  grid-column: 2 / 4;
  grid-row: 2 / 3;
  align-self: start;
  background: linear-gradient(to bottom, #fff5ee, #fff5ee) padding-box,
    linear-gradient(0deg, $rock1, $rock2) border-box;
  &:hover {
    cursor: pointer;
  }
  &-clicked {
    grid-column: 1 / 3;
    grid-row: 1 / 2;
    background: linear-gradient(to bottom, #fff5ee, #fff5ee) padding-box,
      linear-gradient(0deg, $rock1, $rock2) border-box;
  }
  &-cpu {
    grid-column: 3/ 5;
    grid-row: 1 / 2;
    background: linear-gradient(to bottom, #fff5ee, #fff5ee) padding-box,
      linear-gradient(0deg, $rock1, $rock2) border-box;
  }
}

.paper {
  grid-column: 1 / 3;
  grid-row: 1 / 2;
  background: linear-gradient(to bottom, #fff5ee, #fff5ee) padding-box,
    linear-gradient(0deg, $paper1, $paper2) border-box;
  &:hover {
    cursor: pointer;
  }
  &-cpu {
    grid-column: 3/ 5;
    grid-row: 1 / 2;
    background: linear-gradient(to bottom, #fff5ee, #fff5ee) padding-box,
      linear-gradient(0deg, $paper1, $paper2) border-box;
  }
}

.scissors {
  grid-column: 3 / 5;
  grid-row: 1 / 2;
  background: linear-gradient(to bottom, #fff5ee, #fff5ee) padding-box,
    linear-gradient(0deg, $scissor1, $scissor2) border-box;
  &:hover {
    cursor: pointer;
  }
  &-clicked {
    grid-column: 1 / 3;
    grid-row: 1 / 2;
    background: linear-gradient(to bottom, #fff5ee, #fff5ee) padding-box,
      linear-gradient(0deg, $scissor1, $scissor2) border-box;
  }
  &-cpu {
    grid-column: 3/ 5;
    grid-row: 1 / 2;
    background: linear-gradient(to bottom, #fff5ee, #fff5ee) padding-box,
      linear-gradient(0deg, $scissor1, $scissor2) border-box;
  }
}

.empty-circle {
  background: linear-gradient(to bottom, $color-background2, $color-background2)
    padding-box;
  grid-column: 3 / 5;
}

@media (min-height: 568px) {
  .buttons__button {
    max-height: 130px;
  }
}

@media (min-width: 411px) {
  .buttons__button {
    max-height: 150px;
  }
}

@media (max-width: 280px) {
  .buttons__button {
    height: 56%;
  }
}

@media (max-height: 480px) {
  .buttons__button {
    width: 65%;
  }
}

@media (min-width: 540px) {
  .buttons__button {
    width: 62%;
    height: 60%;
    max-height: 300px;
  }
}

@media (min-width: 1024px) {
  .buttons__button {
    width: 50%;
    height: 42%;
    max-height: 300px;
    img {
      width: 40%;
    }
  }
  .rock {
    align-self: center;
    grid-column: 2 / 4;
  }
}

@media (min-width: 1080px) {
  .buttons__button {
    width: 50%;
    height: 60%;
    max-height: 300px;
    img {
      width: 40%;
    }
  }
  .rock {
    align-self: center;
    grid-column: 2 / 4;
  }
}

@media (min-width: 1280px) {
  .buttons__button {
    height: 58%;
  }
}

@media (min-width: 1920px) {
  .buttons__button {
    height: 70%;
  }
}
