.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 2px solid rgba($color: #fff, $alpha: 0.4);
  border-radius: 5px;
  padding: 10px;
  width: 80%;
}

.header__title {
  color: rgb(233, 233, 233);
  text-align: start;
  width: 33%;
  line-height: 0.9;
  font-size: 1.4rem;
}

.score__container {
  background-color: #fff;
  width: 30%;
  height: 70px;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  & h3 {
    color: #2a46c0;
    font-size: 80%;
    letter-spacing: 0.5px;
  }
  & p {
    color: #3b4363;
    font-size: 2.3rem;
    font-weight: 700;
  }
}

@media (min-width: 1280px) {
  .header__container {
    width: 50%;
    border-radius: 10px;
  }

  .header__title {
    padding-left: 20px;
    width: 20%;
    font-size: 2rem;
  }

  .score__container {
    width: 18%;
    height: 90px;
    margin: 8px;
    & p {
      font-size: 2.5rem;
    }
  }
}
